.bg-paper {
  background-color: #FCFAF2;

  .wrapper {
    background-color: transparent;
  }

  .header {
    background-color: transparent;
  }
}

.footer.light {
  text-align: center;
  margin-bottom: 60px;
}

.page-complex {
  .title-block {
    margin-top: 36px;
    text-align: center;

    .subtitle {
      color: #333;
      line-height: 25px;
      margin-bottom: 8px;
    }

    h1 {
      margin-right: 0 !important;
      margin-bottom: .3em;
      font-size: 50px;
      color: #000;
      line-height: 60px;
      text-transform: uppercase;
    }
  }

  .offer-transport-params {
    text-align: center;
    margin-bottom: 25px;

    .item {
      float: none;
      display: inline-block;
      margin: 0 15px;
    }
  }

  hr {
    border: none;
    border-bottom: 1px solid #ccc;
    height: 0;
    background: none;
  }
}

.complex-button {
  background-image: url(../i/stripped.png);
  border: 2px solid #BFD4A4;
  border-radius: 5px;
  display: block;
  transition: .2s;

  &:hover {
    border-color: #BFD4A4 - 25;
    text-decoration: none;
    color: #000;
  }

  &.plans {
    font-size: 20px;
    font-weight: bold;
    height: 90px;

    .inner {
      background-image: url(../i/plans.png);
      background-repeat: no-repeat;
      padding: 21px 0 0 150px;
      height: 90px;
      box-sizing: border-box;
      display: block;
    }
  }

  &.jumbo {
    padding: 50px 30px 68px;
    text-align: center;

    .title {
      font-size: 20px;
      color: #999;
      display: block;
      margin-bottom: 6px;
    }

    .accent {
      font-size: 50px;
      line-height: 60px;
      font-weight: bold;
      display: block;
      color: #63992E;
      white-space: nowrap;

      .pre {
        font-weight: normal;
        font-size: 40px;
        margin-right: .6ex;
        opacity: .6;
      }
    }
  }
}

.complex-categories {
  .title {
    color: #666;
    display: block;
    margin-bottom: .75em;
  }

  >a, 
  >span {
    display: inline-block;
    margin: 0 2.25ex .75em 0;
    font-weight: bold;
    text-decoration: none;
    font-size: 20px;

    span {
      font-weight: normal;
      color: #666;
      margin-left: .8ex;
    }

    &.warn {
      span {
        color: @color-red;
      }
    }
  }
}

.complex-hero {
  margin-bottom: 30px;

  .image {
    height: 530px;
    background: #eee;
  }

  .viewport {
    margin-bottom: 30px;
    position: relative;

    img {
      display: block;
    }

    footer {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      background-color: rgba(51, 51, 51, .9);
      color: #fff;
      padding: 25px 250px 25px 30px;
      box-sizing: border-box;
      text-align: left;
      min-height: 120px;

      a {
        color: @color-yellow;
      }

      .marker {
        box-sizing: border-box;
        position: absolute;
        right: 0;
        top: 0;
        height: 100%;
        width: 160px;
        padding: 20px 0;
        text-align: center;
        display: table-cell;
        font-size: 13px;
        line-height: 15px;

        .label {
          position: relative;
          height: 100%;
          padding: 9px 20px;
          box-sizing: border-box;

          i {
            display: block;
            width: 22px;
            height: 27px;
            margin: 0 auto 5px;
            background-image: url(../i/badge-certified.svg);
          }

          &:before {
            border-left: 1px solid #666;
            content: '';
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
          }
        }
      }
    }
  }
}

.complex-gallery {
  @item-width: 140px;
  @item-height: 105px;
  @item-margin: 11px;

  margin-bottom: 30px;
  position: relative;

  .left,
  .right {
    color: #333;
    border: 2px solid #333;
    border-radius: 40px;
    opacity: .3;
    position: absolute;
    top: 50%;
    margin-top: -25px;
    transition: .2s;
    cursor: pointer;
    width: 50px;
    height: 50px;
    text-align: center;
    line-height: 50px;

    i {
      position: absolute;
      top: 50%;
      left: 50%;
      margin-top: -16px;
    }

    .no-text-selection;

    &.left {
      left: 50px;

      i {
        margin-left: -18px;
      }
    }

    &.right {
      right: 50px;

      i {
        margin-left: -14px;
      }
    }

    &:hover {
      opacity: .5;
    }

    &.disabled {
      opacity: 0;
      cursor: default;
    }
  }

  .frame {
    width: (@item-width + (@item-margin * 2) + 8) * 4;
    margin: 0 auto;
    height: @item-height;
    padding: 0;

    .slidee {
      margin: 0;
      padding: 0;
      height: 100%;
      list-style: none;
      text-align: center;

      li {
        margin: 0 @item-margin;
        width: @item-width;
        height: 100%;
        position: relative;
        display: inline-block;

        .image {
          overflow: hidden;
          width: @item-width;
          height: @item-height;
          border: none;
          position: relative;
          display: block;

          img {
            transition: .5s;
          }
        }

        &:hover {
          .image {
            img {
              transform: scale(1.1);
            }
          }
        }
      }
    }
  }
}

.complex-phone {
  text-align: center;

  i {
    color: #63992E;
    border: 2px solid #BFD4A4;
    border-radius: 41px;
    width: 76px;
    height: 76px;
    font-size: 70px;
    margin: 0 auto 20px;
    background-image: url(../i/stripped.png);
  }

  .num {
    font-size: 46px;
    line-height: 53px;
    font-weight: bold;
    white-space: nowrap;
    margin-bottom: 12px;
  }

  .desc {
    color: #999;
  }
}

.complex-phones {
  position: relative;
  display: block;

  .title {
    text-align: center;
    font-size: 20px;
    margin-bottom: 20px;
  }

  i {
    color: #63992E;
    border: 2px solid #BFD4A4;
    border-radius: 41px;
    width: 76px;
    height: 76px;
    font-size: 70px;
    position: absolute;
    left: 50%;
    margin-left: -40px;
    margin-top: 6px;
    background-image: url(../i/stripped.png);
  }

  .phones {
    .left,
    .right {
      width: 43%;

      .num {
        font-size: 46px;
        line-height: 53px;
        font-weight: bold;
        white-space: nowrap;
        margin-bottom: 12px;
      }

      .desc {
        color: #999;
      }
    }

    .left {
      text-align: right;
      float: left;
    }

    .right {
      float: right;
    }
  }
}

.complex-info-cells {
  .title {
    text-align: center;
    margin-bottom: 25px;
    color: #999;
  }

  table {
    background-image: url(../i/stripped.png);
    width: 100%;
    border-radius: 2px;
    border-spacing: 0;
    border-collapse: separate;
    border: 1px solid #BFD4A4;

    td {
      border: 1px solid #BFD4A4;
      width: 230px;
      height: 140px;

      >a,
      >span {
        width: 1%;
        height: 140px;
        display: table-cell;
        padding: 20px 30px;
        line-height: 25px;
        font-size: 20px;
        font-weight: bold;
        text-align: center;
        vertical-align: middle;
        box-sizing: border-box;
        transition: .2s;

        &:hover {
          background-color: rgba(0, 0, 0, .025);
          text-decoration: none;
          color: #333;
        }
      }
    }
  }
}

.complex-landing-header {
  text-align: center;

  .heading {
    i {
      font-size: 120px;
      display: block;
      margin: 0 auto;
      width: 120px;
      height: 120px;
      color: @color-red;
    }

    .title {
      font-size: 50px;
      line-height: 60px;
      font-weight: bold;
    }

    .subtitle {
      font-size: 50px;
      line-height: 60px;
    }
  }
}


.complex-cat {
  .title {
    font-size: 20px;
    font-weight: bold;
    text-align: center;
  }

  .item {
    text-align: left;
    margin-bottom: 40px;
    display: block;

    &:hover {
      text-decoration: none;
    }

    .title {
      display: block;
      text-align: left;
      margin-bottom: 7px;
    }

    .subtitle {
      display: block;
      color: #666;
    }

    .image {
      display: block;
      margin-bottom: 20px;
      overflow: hidden;

      img {
        transition: .5s;
      }
    }

    &:hover {
      .image {
        img {
          transform: scale(1.1);
        }
      }
    }
  }
}

.complex-map {
  height: 400px;
  background: #eee;

  .links {
    text-align: center;
    font-size: 13px;

    a {
      padding: 4px 16px;
      background-color: rgba(51, 51, 51, .9);
      color: #fff;
      position: relative;
      display: inline-block;
      border-radius: 3px;
      top: -45px;
      transition: .2s;

      &:hover {
        background-color: rgba(51, 51, 51, .8);
        text-decoration: none;
      }
    }
  }

  .map-container {
    height: 400px;
  }

  .map-balloon {
    width: 300px;
    background: #fff;
    box-shadow: 0 1px 3px rgba(0, 0, 0, .25);
    text-align: left;
    position: relative;
    border-radius: 3px;
    transition: all 500ms cubic-bezier(0.680, -0.550, 0.265, 1.550);
    right: -10px;

    &.disabled {
      opacity: 0;
      transform: scale(0);
    }

    .inner {
      background: #fff;
      padding: 20px;
      border-radius: 3px;
      z-index: 2;
      position: relative;
    }

    &:before {
      content: '';
      position: absolute;
      top: 50%;
      left: -7px;
      z-index: 1;
      width: 20px;
      height: 20px;
      margin-top: -10px;
      background: #fff;
      box-shadow: 0 1px 3px rgba(0, 0, 0, .25);
      transform: rotateZ(45deg);
    }
  }
}